@charset "UTF-8";

/* 
Стиль React
BlockName-ElemName_modName_modVal

Имена записываются латиницей.

Имена блоков и элементов пишутся с заглавной буквы. Имена модификаторов — со строчной.

Каждое слово внутри имени пишется с заглавной буквы.

Имя элемента отделяется от имени блока одним дефисом (-).

Разделители имени и значения модификаторов совпадают с классической схемой.
*/

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.ttf");
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.ttf");
  font-weight: 700;
}

body {
  font-family: "Roboto", serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
}

body.theme_dark {
  color: #fcfbf7;
}

body.theme_light {
  color: #261c00;
}

.fontColor_gray {
  color: #918f8a;
}

.fontColor_grayLight {
  color: #bfbfbf;
}

.fontType_headline {
  font-weight: bold;
  font-size: 30px;
  line-height: 32px;
}

.fontType_subhead {
  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
}

.fontType_caption {
  font-size: 14px;
  line-height: 16px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

body.theme_dark {
  background: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(19, 14, 0, 0.9) 0%, #000000 100%);
}

body.theme_light {
  background: #fff;
}

.HeaderWrapper {
  margin-top: 20px;
}

.Header {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.Header-Title {
  margin-bottom: 8px;
}

.LeadersBodyWrapper {
  flex-grow: 1;
  display: flex;
}

.LeadersBody {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.LeadersBody-Pedestals {
  display: flex;
  align-items: flex-end;
}

.LeadersBody-Pedestal_1 {
  z-index: 5;
}

.LeadersBody-Pedestal_2,
.LeadersBody-Pedestal_3 {
  z-index: 3;
  margin-right: -16px;
  margin-left: -16px;
}

.LeadersBody-PersonWrapper {
  margin-bottom: 16px;
}

.LeadersBody-Block {
  text-align: center;
  padding-top: 18px;
  height: 68px;
  width: 120px;
  box-sizing: border-box;
  border-radius: 6px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) -2.22%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 95.38% at 38.75% 71.48%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6);
}

.LeadersBody-Block_1 {
  height: 108px;
  background: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.4);
}

.LeadersBody-Block_2,
.LeadersBody-Block_3 {
  height: 88px;
}

.Person {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Person-AvatarWrapper {
  position: relative;
  display: block;
}

.Person-AvatarWrapper::before {
  position: absolute;
  top: -22px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  content: attr(data-emoji);
  font-size: 30px;
  line-height: 32px;
  z-index: 5;
}

.Person-Avatar {
  border-radius: 50%;
  filter: grayscale(1);
}

.Person-Name {
  display: inline-block;
  max-width: 104px;
  text-align: center;
  margin-top: 6px;
}

.Person-Value {
  margin-top: 4px;
}